/* Home.css */

#searchBox {
  width: 300px;
  float: left;
}

/* Styles for the container div */
#cakeHomeBody {
  padding: 20px;
  background-color: #ffffff;
  height: 500px;
  width: 90%;
  margin: 20px auto;
}

/* Styles for the h1 header */
h1 {
  font-size: 24px;
  color: #333;
}

/* Styles for the buttons */
.viewcakebutton {
  padding: 10px 15px !important;
  margin: 10px 5px;
  border: none;
  background-color: #007BFF;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  font-weight: bold;
}

.viewcakebutton:hover {
  background-color: #0056b3;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

/* Styles for the search box and sort buttons */
#search-sort-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.interestRate {
  float: left;
}

#sort-buttons button {
  float: left;
  background-color: #333;
  color: #fff;
  width: 100px; /* Adjusted width for sort buttons */
}

/* Styles for the table */
.cake-table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ccc;
}

.cake-table th, .cake-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center !important;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.cake-table thead th {
  background-color: #3498db;
  color: white;
  cursor: pointer;
}

.sortButtons {
  background-color: white;
  padding: 2px !important;
  float: left;
  margin-top: 1px;
}

#interestrate {
  float: left;
}

/* Styles for the pagination buttons */
#pagination-container {
  width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px auto;
}

/* Styles for the delete confirmation popup */
.delete-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  text-align: center;
  z-index: 2; /* Above the overlay */
}

.delete-popup p {
  margin: 0;
  padding: 20px;
}

.delete-popup button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px;
}

.delete-popup button:hover {
  background-color: #e60000; /* A slightly darker shade of red */
}

.delete-popup button:last-child {
  background-color: #ccc; /* Gray color for Cancel button */
}

.delete-popup button:last-child:hover {
  background-color: #999; /* A slightly darker shade of gray for Cancel button on hover */
}

.no-records-cell {
  text-align: center !important;
  background-color: #f0f0f0; /* Set the background color to your preferred color */
  padding: 200px; /* Add some padding for spacing */
  color: #333; /* Set the text color */
  font-size: 18px; /* Set the font size */
  font-weight: bold; /* Make the text bold */
}

/* Styles for the delete button */
#deleteButton {
  padding: 10px 15px !important;
  margin: 10px 5px;
  border: none;
  background-color: #e60000;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  font-weight: bold;
}

#deleteButton:hover {
  background-color: #e41212;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

.greenButton {
  background-color: green;
}

/* Styles for cake images */
.cake-image {
  width: 100px; /* Fixed width for cake images */
  height: 100px; /* Fixed height for cake images */
  object-fit: cover; /* Maintain aspect ratio and cover the area */
}
