  /* LoanApplicationForm.css */
  .container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f7f7f7;
  }
  
  h2 {
    text-align: center;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  .error {
    color: #ff0000;
    font-size: 12px;
  }
  
  .submit-button {
    background-color: #007BFF;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
  }
  
  .button-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  td,tr{
    padding: 10px !important;
  }
  
  button  {
    padding: 10px 15px !important;
    margin: 10px 5px;
    border: none;
    background-color: #007BFF;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 5px 15px rgba(0,0,0,0.2);
    border-radius: 5px;
    font-weight: bold;
  }
  
  button:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.4);
  }
  
  
  
  .disabled-button {
    background-color: #cccccc !important;
    color: #666666 !important;
  }
   